@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700;900&display=swap');

:root {
  font-family: 'Inter', sans-serif;
  --fw-regular: 400;
  --fw-bold: 700;
  --fw-bolder: 900;

  --clr-red-50: hsl(5, 85%, 63%);
  --clr-orange-50: hsl(35, 77%, 62%);

  --clr-white: hsl(36, 100%, 99%);
  --clr-blue-10: hsl(233, 8%, 79%);
  --clr-blue-50: hsl(236, 13%, 42%);
  --clr-blue-90: hsl(240, 100%, 5%);
}
